* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #fafafa;
}

body .bg-faded {
  background-color: white;
}

body .navbar {
  border-bottom: 3px solid #00aaff;
  margin-bottom: 1.3rem;
}

body .navbar .navbar-brand:before {
  content: '#';
  color: #3897f0;
}

body .navbar .navbar-nav .nav-item .nav-link.active {
  color: #007bff;
}

@media (max-width: 991px) {
  body .navbar .container {
    padding-right: 0;
    padding-left: 0;
  }
}

@media (max-width: 576px) {
  body .navbar .container {
    position: initial;
    margin-left: 0;
  }
}

body #search {
  margin-bottom: 0;
}

body .card {
  margin-bottom: 1rem;
}

body .card .btn-block {
  margin-top: .5rem;
  white-space: inherit;
  transition: background-color .4s ease-out;
}

body .card .nav.flex-column {
  width: 100%;
}

body .card .nav.flex-column .nav-link {
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: .05rem;
  padding-bottom: .1rem;
}

body .container blockquote,
body .container iframe,
body .container .instagram-media {
  max-width: 100% !important;
}

body .container article blockquote {
  border-left: 2px solid #28a745;
  padding-left: 1rem;
}

body .container article ol {
  margin-left: 1rem;
  margin-right: 1rem;
}

.badge {
  font-weight: 500;
}

.badge.badge-primary {
  border: 2px solid #007bff;
  background: transparent;
  color: #060606;
}

.badge.badge-success {
  border: 2px solid #28A845;
  background: transparent;
  color: #000;
}

.bx-fa-style {
  color: rgba(19, 132, 255, 0.55);
}

.footer-distributed {
  background-color: #292c2f;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  width: 100%;
  text-align: left;
  font: normal 16px sans-serif;
  padding: 45px 50px;
  margin-top: 80px;
}

.footer-distributed .footer-social a {
  display: inline-block;
  width: 35px;
  height: 35px;
  background-color: #33383b;
  border-radius: 2px;
  font-size: 20px;
  color: #ffffff;
  text-align: center;
  line-height: 35px;
  margin-left: 3px;
}

.footer-distributed .footer-links a {
  font-size: 18px;
  font-weight: bold;
  color: #ffffff;
  margin: 0 0 10px;
  padding: 0;
}

.footer-distributed .footer-company,
.footer-distributed .footer-company a {
  color: #8f9296;
  font-size: 14px;
  margin: 0;
}

